import React, { useEffect, useState } from "react";
import { createPopper } from "@popperjs/core";
import { dashboardActions } from "../../../../../Actions";
import { useDispatch } from "react-redux";
import { Button, Modal } from "react-bootstrap";

const TableDropdownInventory = (props) => {
  const [show, setShow] = useState(false);
  const [show1, setShow1] = useState(false);
  const [state, setState] = useState({
    remarks: "",
    errorMessage: "",
    quantityApproved: "",
  });
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const handleClose1 = () => setShow1(false);
  const handleShow1 = () => setShow1(true);
  const [dropdownPopoverShow, setDropdownPopoverShow]: any =
    React.useState(false);
  const btnDropdownRef: any = React.createRef();
  const popoverDropdownRef: any = React.createRef();
  const openDropdownPopover = () => {
    createPopper(btnDropdownRef.current, popoverDropdownRef.current, {
      placement: "left-start",
    });
    setDropdownPopoverShow(true);
  };
  const closeDropdownPopover = () => {
    setDropdownPopoverShow(false);
  };
  const dispatch = useDispatch();
  console.log(dropdownPopoverShow);
  const approveInventory = async () => {
    const data = {
      approvalLevel: "APPROVED",
      remarks,
      quantityApproved: props?.company_info?.quantityStock,
    };
    await dispatch(
      dashboardActions.approveInventory(data, props.company_info.id)
    );
  };
  const rejectInventory = async () => {
    const data = {
      approvalLevel: "REJECTED",
      remarks,
      quantityApproved: props?.company_info?.quantityStock,
    };
    await dispatch(
      dashboardActions.rejectInventory(data, props.company_info.id)
    );
  };
  const onchange = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
      errorMessage: "",
    });
  };
  useEffect(()=>{
   setState({
     ...state,
     quantityApproved: props?.company_info?.quantityStock,
   }) 
  },[])
  const { remarks, quantityApproved } = state;
  console.log(props.company_info);
  return (
    <>
      <a
        className="text-blueGray-500 py-1 px-3 colreverse"
        ref={btnDropdownRef}
        onClick={(e) => {
          e.preventDefault();
          dropdownPopoverShow ? closeDropdownPopover() : openDropdownPopover();
        }}
      >
        <svg
          width="4"
          height="16"
          viewBox="0 0 4 16"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <mask
            id="mask0_50_573"
            maskUnits="userSpaceOnUse"
            x="0"
            y="0"
            width="4"
            height="16"
          >
            <path
              fill-rule="evenodd"
              clip-rule="evenodd"
              d="M2 4C3.1 4 4 3.1 4 2C4 0.9 3.1 0 2 0C0.9 0 0 0.9 0 2C0 3.1 0.9 4 2 4ZM2 6C0.9 6 0 6.9 0 8C0 9.1 0.9 10 2 10C3.1 10 4 9.1 4 8C4 6.9 3.1 6 2 6ZM0 14C0 12.9 0.9 12 2 12C3.1 12 4 12.9 4 14C4 15.1 3.1 16 2 16C0.9 16 0 15.1 0 14Z"
              fill="white"
            />
          </mask>
          <g mask="url(#mask0_50_573)">
            <rect x="-10" y="-4" width="24" height="24" fill="#9EA0A5" />
          </g>
        </svg>
      </a>
      {dropdownPopoverShow && (
        <div className="reltv">
          <div
            ref={popoverDropdownRef}
            className={
              (dropdownPopoverShow ? "block " : "hidden ") +
              "bg-white text-base z-50 float-left py-2 list-none text-left rounded shadow-lg min-w-48 colreverse"
            }
          >
            {/* <span
              className={
                "text-sm py-2 px-4 font-normal block w-full whitespace-nowrap bg-transparent text-blueGray-700"
              }
            >
              Edit
            </span> */}
            {/* {!props.company_info?.is_confirmed && (
              <a
                href="#pablo"
                className={
                  "text-sm py-2 px-4 font-normal block w-full whitespace-nowrap bg-transparent text-blueGray-700"
                }
              >
                Confirm
              </a>
            )} */}

            {/* {props.company_info?.deleted_at && (
              <a
                href="#pablo"
                className={
                  "text-sm py-2 px-4 font-normal block w-full whitespace-nowrap bg-transparent text-blueGray-700"
                }
              >
                Restore
              </a>
            )} */}
            {/* <a
                href={`/admin/staffs/${props?.company_info?.id}/ORGANISATION`}
                className={
                  "text-sm py-2 px-4 font-normal block w-full whitespace-nowrap bg-transparent text-blueGray-700"
                }
              >
                View Staffs
              </a> */}
            <a
              href={`/admin/inventory/edit/${props?.company_info?.id}`}
              className={
                "text-s12m text-sm py-2 px-4 font-normal block w-full whitespace-nowrap bg-transparent text-blueGray-700"
              }
            >
              Edit
            </a>
            {props?.company_info?.approval !== "APPROVED" && props?.company_info?.approval !== "REJECTED" &&(
              <span
                className={
                  "text-s12m text-sm py-2 px-4 font-normal block w-full whitespace-nowrap bg-transparent text-blueGray-700"
                }
                onClick={handleShow}
              >
                Approve
              </span>
            )}
            {props?.company_info?.approval !== "REJECTED" && props?.company_info?.approval !== "APPROVED" && (
              <span
                className={
                  "text-s12m text-sm py-2 px-4 font-normal block w-full whitespace-nowrap bg-transparent text-blueGray-700"
                }
                onClick={handleShow1}
              >
                Reject
              </span>
            )}
          </div>
        </div>
      )}
      {/* Approval modal */}
      <Modal show={show} onEnter={closeDropdownPopover} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Approve Inventory</Modal.Title>
        </Modal.Header>

        <Modal.Body>
          <span className="rdfrmlbl">
            {" "}
            Reason<span className="text-danger">*</span>
          </span>
          <textarea
            name="remarks"
            onChange={onchange}
            className="form-control forminput"
          ></textarea>
          <br/>
            <span className="rdfrmlbl">
            {" "}
            Quantity<span className="text-danger">*</span>
          </span>
          <input
            type="number"
            value={quantityApproved}
            name="quantityApproved"
            onChange={onchange}
            className="form-control forminput"
          />
          <br/>
        </Modal.Body>

        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={approveInventory}>
            Approve
          </Button>
        </Modal.Footer>
      </Modal>
      <Modal show={show1} onEnter={closeDropdownPopover} onHide={handleClose1}>
        <Modal.Header closeButton>
          <Modal.Title>Reject Inventory</Modal.Title>
        </Modal.Header>

        <Modal.Body>
          <span className="rdfrmlbl">
            {" "}
            Reason<span className="text-danger">*</span>
          </span>
          <textarea
            name="remarks"
            onChange={onchange}
            className="form-control forminput"
          ></textarea>
        </Modal.Body>

        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose1}>
            Close
          </Button>
          <Button variant="danger" onClick={rejectInventory}>
            Reject
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default TableDropdownInventory;
